import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { DualListComponent } from 'angular-dual-listbox';
import { UserService } from 'src/app/service/user.service';
import { EditRole, SessionUser } from 'src/app/models/login';
import { forkJoin, merge, mergeMap } from 'rxjs';
import { Response } from 'src/app/models/response';
import { LoginService } from 'src/app/service/login.service';
import { Role } from '../../../../models/login';
import { Util, Messages } from 'src/app/utilities/util';



@Component({
  selector: 'app-perfiles',
  templateUrl: './perfiles.component.html',
  styleUrls: ['./perfiles.component.css']
})
export class PerfilesComponent implements OnInit {

  @Input()
  idUser = null;
  statusButton: boolean = false;

  
  @Input()
  set nameBtn(val){
    this.btnName = val
    this.statusButton = val.length > 0 ?? false
  }

  get getBtName(){
    return this.btnName
  }

  private btnName;
  tab = 1;
  keepSorted = true;
  key: string;
  display: string;
  filter = true;
  source: Array<any>;
  confirmed: Array<any>;
  userAdd = '';
  disabled = false;

  sourceLeft = true;
  // format: any = DualListComponent.DEFAULT_FORMAT;
	
  DEFAULT_FORMAT = { add: 'Agregar', remove: 'Remover', all: 'Seleccionar todos', 
  none: 'None', direction: DualListComponent.LTR, draggable: true };
  // format = this.DEFAULT_FORMAT;
  format = { add: 'Agregar', remove: 'Remover', all: 'Seleccionar todos', none: 'Deseleccionar',
  direction: DualListComponent.RTL, draggable: true, locale: 'da' };
  
  private sourceStations: Array<Role> = [];

  private confirmedStations: Array<Role> = [];
  nameComponent: any;
  userData: SessionUser;

  @Input()
  set name(val){
    this.nameComponent = val;
    if(this.nameComponent){
      
    }
  }

  private stations: Array<Role> = [];

  constructor( private userService:UserService, private loginService: LoginService, private ref: ChangeDetectorRef){}


  ngOnInit() {

    forkJoin(
      this.userService.getById<Response<SessionUser>>(this.idUser),
      this.loginService.getPerfiles<Response<Array<Role>>>()
      ).subscribe( ([user, roles]) =>{
        console.log(user);
        console.log(roles);
        this.sourceStations = roles.data;
        if(user.data.listRoles){
          this.confirmedStations = user.data.listRoles;
        }
        this.useStations();
    })    
    this.userService.getById<Response<SessionUser>>(this.idUser).subscribe((response)=>{
      console.log(response)
      this.userData = response['data'];
    })
  }

  private useStations() {
    this.key = 'idRole';
    this.display = 'role'; // [ 'idRole', 'role' ];
    this.keepSorted = true;
    this.source = this.sourceStations; // son los roles disponibles
    this.confirmed = this.confirmedStations; // son los que seran removidos
  }



  guardar(){
    let temp:Array<Role> = [...this.source]
    let temp2:Array<Role> = [...this.confirmed];
    let _$ = new EditRole();
    let arr = []
    for(let i = 0; i < temp.length; i ++){
      let boo = false;
      for(let j = 0 ; j < temp2.length; j++){
        if(temp[i] === temp[j]){
          boo = true
          break;
        }
      }
      if(boo == false){
        arr.push(temp[i])
      }
      _$.agregar = temp2;
      _$.remover = arr;
      _$.idUser = this.idUser;
    }
    this.loginService.putPerfiles(_$).subscribe((response)=>{
      let util = new Util();
      let msg = new Messages();
      util.bottomStartOk(msg.MSG_EMERGENTE_TRANSACCION_OK);
    });
  
    console.log(arr);
    

    
  }

  filterBtn() {
    return (this.filter ? 'Hide Filter' : 'Show Filter');
  }

  doDisable() {
    this.disabled = !this.disabled;
  }

  disableBtn() {
    return (this.disabled ? 'Enable' : 'Disabled');
  }

  swapDirection() {
    this.sourceLeft = !this.sourceLeft;
    this.format.direction = this.sourceLeft ? DualListComponent.LTR : DualListComponent.RTL;
  }





}
