import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { UsuariosListComponent } from './usuarios/usuarios-list/usuarios-list.component';
import { AuthGuardGuard } from 'src/app/utilities/auth-guard.guard';
import { RoleBaseAuthorization } from '../../models/login';
import { PerfilComponent } from '../perfil/perfil.component';
import { VistaUsuarioComponent } from './usuarios/vista-usuario/vista-usuario.component';
import { UsuarioComponent } from './usuarios/usuario/usuario.component';
import { UsuariosComponent } from './usuarios.component';
import { PrivilegioGuardGuard } from 'src/app/utilities/privilegio-guard.guard';

const routes: Routes = [
  {
    path: '', component : UsuariosComponent,
    children:[
      {path:'usuario/:id', component: VistaUsuarioComponent, 
      canActivate:[AuthGuardGuard, PrivilegioGuardGuard],
      data:{
        breadcrumb:"Usuario",
        privilegio:"108",
        roles: [
          RoleBaseAuthorization.ADMIN,
          RoleBaseAuthorization.COORDINADOR,
          RoleBaseAuthorization.EJECUTIVO_PROYECTOS,
        ]
      }},
      {path:'', component: UsuariosListComponent}
    ]
  },
  // {path:'' , redirectTo:"/",pathMatch:"full"}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UsuariosRoutingModule { }
