import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from '../../../../service/user.service';
import { filterObject, filterObject2 } from 'src/app/models/filterObject';
import { FilterComponent } from '../../../../share-modules/filter/filter.component';
import { FilterResponse } from '../../../../models/usuario';
import { AllEvaluation, TimePeriod } from '../../../../models/reports';
import { faBriefcaseClock } from '@fortawesome/free-solid-svg-icons';
import { RoleBaseAuthorization, Privilegios } from '../../../../models/login';
import { filter } from 'rxjs/operators';
import { VistaUsuarioComponent } from '../vista-usuario/vista-usuario.component';

@Component({
  selector: 'app-usuarios-list',
  templateUrl: './usuarios-list.component.html',
  styleUrls: ['./usuarios-list.component.css']
})
export class UsuariosListComponent implements OnInit {
  cabecera:Array<any>=[];
  cabeceraFilter:Array<any>=[];
  filas:Array<any>=[];
  deleteReference :Array<string>=[]; 
  idFilter :string='filtro_usuarios';
  filterList: filterObject[] = [];
  paginado = 10;
  dataReference=[];
  perfiles: RoleBaseAuthorization[]=[]
  mask: String = "";
  data: any;
  constructor(private userService: UserService, private router: Router , private route: ActivatedRoute, ) { 
    this.router.routeReuseStrategy.shouldReuseRoute = () => {
      return false;
    };
  }
  // save: FilterComponent
  save: any = {
    guardar: true,
    borrar: false
  };

  ngOnInit(): void {
    // this.userService.getDataUsers().subscribe((result)=>{
    //   this.filas = result['data']['listUsers']
    //   result.data.config.forEach(element => { 
    //     if(element.isVisible == 1){
    //     this.cabecera.push(element.headerGrid);        
    //   }else
    //     this.deleteReference.push(element.headerGrid);        
    //   });
    //   this.deleteData();
    //   this.setFilterConfig();
    //   this.paginado = result['data']['maxcount']
    // })
    this.userService.getDataUsersByConf(4).subscribe((result)=>{
      console.log(result);
      result.data.config.forEach((element) => {
        this.cabecera.push(element.headerGrid);
      });
      this.data=result.data;
    })

  }

  setFilterConfig() {
    let sistema = this.cabecera[5];
    let password = this.cabecera[6];
    
    this.cabecera.forEach(element => {
      if(element != password && element != sistema){
      this.filterList.push(this.setFilter(element,element, 'Ingrese'+' '+ element.toLowerCase(), 'text', null, null, ' ', ' '));
      }else 
      this.filterList.push(this.setFilter(element,element, 'Ingrese'+' '+ element.toLowerCase(), 'date', null, null, ' ', ' '));
    });
  }

  setFilter(id: any, label: any, placeholder: any, typeInput: any, list: any, listKey: any, icon: any, pattern: any): filterObject {
    const filterObj: filterObject = new filterObject();
    filterObj.id = id;
    filterObj.label = label;
    filterObj.placeholder = placeholder;
    filterObj.type = typeInput;

    return filterObj;
  }
  
  deleteData(){
    console.log(this.deleteReference);
    
    this.deleteReference.forEach(element => {
      if(element == this.deleteReference[0]){
        let element2 = element.split(' ')[1].toLowerCase();
        this.deleteProperty(element2)
      }else
        if(element==this.deleteReference[1]){
         var element3 = element.split(' ')[0].toLowerCase();
         this.deleteProperty(element3)   
      }
    });
  }

  deleteDataFilter(){
    this.deleteReference.forEach(element => {
      if(element == this.deleteReference[0]){
        let element2 = element.split(' ')[1].toLowerCase();
        this.deleteProperty(element2)
      }else
        if(element==this.deleteReference[1]){
         var element3 = element.split(' ')[0].toLowerCase();
         this.deleteProperty(element3)   
      }
    });
  }

  getValuesFilter(event){
    let filters: Array<filterObject2> = new Array<filterObject2>();
    event.forEach((element, i) => {
      let model: filterObject2 = new filterObject2();
      model.id = element.id;
      if(element.value == undefined)
        model.value = "";
      else
        model.value = element.value;

      filters.push(model);
    });
    
    let map: string = "{";
    filters.forEach(element => {
       map = map + '"' + element.id.toString() + '":' + '"' + element.value.toString() + '"' + ','
       map=map.replace(/ /g, "_");
    });

    map=map.substring(0,(map.length-1))
    map=map + "}"
    
    var json = JSON.parse(map);
    console.log(json);
    
    // this.saveFiltro = json;

    this.userService.getDataFilter(json).subscribe((result)=>{
      this.filas = result['data']['listUsersDto']
      let cabecerasFilter = result['data']['config']     
      
      cabecerasFilter.forEach(element => {
        if(element.isVisible){
          this.cabeceraFilter.push(element.headerGrid)
        }else{
          this.deleteReference.push(element.headerGrid)  
        }
      });
      this.deleteDataFilter();
    })
  }

  deleteProperty(property){
    for(let i=0;i<this.filas.length;i++){
      if(Reflect.has(this.filas[i], property)){
        delete this.filas[i][property];        
      }
    }
  }

  getDataForList(event:any, id?){
    let $_id = (event == null || event == undefined) ? id : event.id ;
    this.router.routeReuseStrategy.shouldReuseRoute = () => {return false};
    this.router.onSameUrlNavigation = 'reload';
    this.router.navigateByUrl('/home/administracion/usuarios/usuario/'+$_id);
    // this.route.navigate(['/home/administracion/usuarios/usuario/'+$_id]).then((res)=>{
    //   window.location.reload();
    // })
  }

  getCheckBoxData(event){
    console.log(event);
    this.dataReference.length = 0;
    this.dataReference = event;
  }

  viewUser(){
    if(this.dataReference.length == 1){
      this.getDataForList(null, this.dataReference[0].id)
    }
  }

  getEventIcon(event:Privilegios){
    switch(event.idprivilegio){
      case 103: this.viewUser();
    }
  }
}
