import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from 'src/app/service/user.service';
import { SessionUser } from 'src/app/models/login';
import { PrivilegiosServiceService } from 'src/app/service/privilegios-service.service';
import { Response } from 'src/app/models/response';
import { Privilegios } from '../../../../models/login';
import { isEmpty } from 'rxjs';
@Component({
  selector: 'app-vista-usuario',
  templateUrl: './vista-usuario.component.html',
  styleUrls: ['./vista-usuario.component.css']
})
export class VistaUsuarioComponent implements OnInit {
  statusGenerales = 1
  id= 0;
  referenceNumber: any;
  userData:SessionUser = null;
  isVisible
  nameComponent: string;
  nameButton: string;
  data:SessionUser=null;

  contrasenaComponent:string = '';
  butonContranseName:string = '';
  pass;
  restablecerPassComp: Privilegios;
  perfilesComp: Privilegios = null;
  name: string;
  nameBtnPerfil: string='';
  showPefilesComponent: boolean = false;

  constructor(private router: Router,private route: ActivatedRoute, private userService:UserService,
    private privilegiosService : PrivilegiosServiceService) { }

  ngOnInit(): void {

    this.route.params.subscribe(params => {
      this.referenceNumber = params.id
      this.userService.getById<Response<SessionUser>>(params.id).subscribe((response)=>{
        console.log(response)
        this.userData = response['data'];
        this.data = response['data']
        this.userData.userName = response.data.perfil;
      })
    })

    setTimeout(()=>{
      console.log(this.privilegiosService.getAllPrivilegiosArr())
      let temp =[...this.privilegiosService.getAllPrivilegiosArr()]
      let privilegioGeneral = new Privilegios(this.privilegiosService.searchTree(temp[1], 109))
      this.isVisible = privilegioGeneral.status
      this.nameComponent = privilegioGeneral.etiqueta
      let infoHijos = new Privilegios(this.privilegiosService.searchTree(temp[1], 110))
      this.nameButton = infoHijos.etiqueta     
     
      this.restablecerPassComp = new Privilegios(this.privilegiosService.searchTree(temp[1], 112))  
      this.contrasenaComponent =  new Privilegios(this.privilegiosService.searchTree(temp[1], 112)).etiqueta
      this.butonContranseName = new Privilegios(this.privilegiosService.searchTree(temp[1], 113)).etiqueta
      this.perfilesComp =  new Privilegios(this.privilegiosService.searchTree(temp[1], 150))  
      this.showPefilesComponent = Object.keys(this.perfilesComp).length > 0
      this.name = this.perfilesComp.etiqueta
      this.nameBtnPerfil = new Privilegios(this.privilegiosService.searchTree(temp[1], 151)).etiqueta
    
    
      
    },5000)
  

  }

}
