import { AfterViewInit, Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, Validators, AbstractControl } from '@angular/forms';
import { Privilegios, SessionUser } from 'src/app/models/login';
import { PrivilegiosServiceService } from 'src/app/service/privilegios-service.service';
import { UserService } from 'src/app/service/user.service';
import { Util, Messages } from 'src/app/utilities/util';

@Component({
  selector: 'app-usuario',
  templateUrl: './usuario.component.html',
  styleUrls: ['./usuario.component.css']
})
export class UsuarioComponent implements OnInit, AfterViewInit {
  
  private userData: SessionUser;
  
  @Input()
  nameComponent: string;
  
  @Input()
  set nameButton(val:string){
    this.nameBtn = val;
    this.statusButton = val.length > 0 
  }
  statusButton:boolean= false;
  nameBtn:string = null;

  @Input()
  set user(val:SessionUser){

    this.userData = val;
    this.formularioGeneral = this.getFormularioGeneral(val);
 
  }

  disabledUserName=true;
  arrPrivilegios :Array<Privilegios>;
  
  
  statusPass=0
  // nameComponent: string;
  constructor(private _formB: FormBuilder,private userService:UserService,
    private privilegiosService: PrivilegiosServiceService) {}
  
  ngAfterViewInit(): void {
    // throw new Error('Method not implemented.');

  }

  public formularioGeneral = this._formB.group({
    idUser: new FormControl(),
    userName: new FormControl(),
    name: new FormControl(),
    firstLastName: new FormControl(),
    secondLastName: new FormControl(),
    email: new FormControl(),
    estatus: new FormControl(),
    lastLoginDate : new FormControl()
  });

  ngOnInit(): void {


  }

  get user():SessionUser{
    return this.userData;
  }
  getFormularioGeneral(sessionUser?: SessionUser) {
    const dataUsuario = this._formB.group({
      idUser: [sessionUser == null ? null : sessionUser.idUser, [Validators.nullValidator]],
      userName: [sessionUser == null ? null : sessionUser.userName , [Validators.required]],
      name: [sessionUser == null ? null : sessionUser.name, [Validators.required]],
      firstLastName: [
        sessionUser == null ? null : sessionUser.firstLastName,[Validators.required]
      ],
      secondLastName: [
        sessionUser == null ? null : sessionUser.secondLastName,[Validators.nullValidator]
      ],
      email: [
        sessionUser == null ? null : sessionUser.email,[Validators.email, Validators.required]
      ],
      lastLoginDate :[
        sessionUser == null ? null : sessionUser.lastLogin,[Validators.nullValidator]
      ],
      estatus :[
        sessionUser == null ? null : sessionUser.estatus =="Activo" ? true : false,[Validators.nullValidator]
      ],
    });
    return dataUsuario;
  }

  public getFormControl(controlName: string): AbstractControl {
    return this.formularioGeneral.controls[controlName];
  }


  public guardar(){
    console.log(this.formularioGeneral.value)
    if(this.formularioGeneral.valid){
      let util = new Util();
      let msg = new Messages();
      this.userService.upDateData(this.formularioGeneral.value).subscribe((response)=>{
        let data = response['data']
        this.getFormularioGeneral(data)
        this.ngOnInit();
        util.bottomStartOk(msg.MSG_EMERGENTE_TRANSACCION_OK)

      },error =>{
        util.bottomEndError("error")
      }
      )
    }
  }


}
