import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { SessionUser } from 'src/app/models/login';
import { UserService } from 'src/app/service/user.service';
import { Util, Messages } from '../../../../utilities/util';

@Component({
  selector: 'app-restablecer-pass',
  templateUrl: './restablecer-pass.component.html',
  styleUrls: ['./restablecer-pass.component.css']
})
export class RestablecerPassComponent implements OnInit {
  private butonName:string = '';
  private showBtn = false  
  @Input()
  nameComponent = null
  userData: SessionUser;

  @Input()
  set nameButon(val:string){
    
    this.butonName = val;
    if(this.butonName.length > 0 && this.butonName != null && this.butonName != undefined){
      this.showBtn = true
    }
  }
  public formulario = this._formB.group({
    idUser : new FormControl(),
    modificationDate : new FormControl(),
    restablecer: new FormControl()
  });

  @Input()
  set user(val:SessionUser){
    console.log(val)
    this.userData = val;
    this.formulario = this.getFormularioGeneral(val);
    this.formulario.get('restablecer').valueChanges.subscribe((value)=>{
      console.log('====================================');
      console.log(value);
      console.log('====================================');
      this._userService.putResetStatusPass(this.formulario.get('idUser').value, value).subscribe((response)=>{
        let util = new Util();
        let msg = new Messages();
        util.bottomStartOk(msg.MSG_EMERGENTE_TRANSACCION_OK);
      })
    })
  }



  
  constructor(private _formB :FormBuilder, public _userService :UserService) { }

  ngOnInit(): void {
  }

  getFormularioGeneral(sessionUser?: SessionUser) {
    const dataUsuario = this._formB.group({
      idUser:[sessionUser == null ? null : sessionUser.idUser],
      modificationDate :[
        sessionUser == null ? null : sessionUser.passModificationDate,[Validators.nullValidator],
      ],
      restablecer :[
        sessionUser == null ? null : sessionUser.statusPass == 'Activo' ? true : false ,[Validators.nullValidator]
      ]
    });
    return dataUsuario;
  }

  get getNameButon(){
    return this.butonName
  }

  get showButon(){
    return this.showBtn;
  }

}
